#search-section {
    position: relative;
    z-index: 10;
}

#search-form-main__holder {
    padding: rem-calc(10);
    background-color: $main-site-color;
    position: relative;

    .search-summary__row {
        margin-right: -5px;
        margin-left: -5px;

        .search-summary__col {
            padding-right: 5px;
            padding-left: 5px;

            .form-control[readonly] {
                background-color: $color-white;
            }

            .form-control[disabled] {
                background-color: $color-gray-10;
                color: $color-gray-30;
            }

            .input-group > .input-group-prepend > .btn {
                background-color: $color-green;
                color: $color-white;
                border: 1px solid $color-white;
                width: rem-calc(40);
                padding-left: rem-calc(5);
                padding-right: rem-calc(5);
            }

            .disabled-item {
                .input-group > .input-group-prepend > .btn {
                    background-color: $color-gray-20;
                    color: $color-gray-40;
                    border: 1px solid $color-gray-10;
                }
            }
        }
    }

    .search-btn__holder {
        position: relative;
        padding-left: rem-calc(50);

        .search-btn {
            width: 100%;
            padding-top: rem-calc(10);
            padding-bottom: rem-calc(10);

            &[disabled] {
                cursor: default;
                background-color: $color-gray-20;
                color: $color-gray-40;

                &:hover {
                    background-color: $color-gray-20;
                    color: $color-gray-40;
                }
            }
        }
    }

    #filter_options {
        text-align: center;
        font-size: rem-calc(26);
        line-height: rem-calc(40);
        position: absolute;
        width: rem-calc(40);
        height: rem-calc(40);
        padding: 0;
        top: 0;
        left: 0;
        background-color: $color-white;
        color: $color-green;
        border-color: $color-green;

        &:disabled {
            opacity: 1;
            background-color: $color-gray-20;
            border-color: $color-gray-20;
            color: $color-gray-40;
            cursor: default;
        }
    }

    #select-from-city__holder {
        position: absolute;
        top: rem-calc(-30);
        padding: rem-calc(5);
        background-color: $main-site-color;

        .form-control {
            height: rem-calc(25);
            border: none;
            background-color: transparent;
            color: #fff;
            padding-top: rem-calc(3);
            padding-bottom: rem-calc(3);

            option {
                color: #000;
            }

            &:focus {
                box-shadow: none;
            }
        }
    }
}

.search-form-step__section {
    .search-form-step__header {
        text-align: center;
        .cbp_tmicon {
            width: 48px;
            height: 48px;
            font-size: 24px;
            line-height: 48px;
            -webkit-font-smoothing: antialiased;
            color: #e04f67;
            background:#f9f9f9;
            border-radius: 50%;
            box-shadow: 0 0 0 3px #e04f67;
            text-align: center;
            margin-bottom: rem-calc(15);
            margin-top: rem-calc(15);
        }
    }
}

#search-form-toggle__holder {
    .form-data-toggle-target {
        background-color: #fff;
        margin-top: rem-calc(10);
        padding: rem-calc(10);
        border: 1px solid #ccc;
        h3 {
            margin-top: 0;
        }
    }
}

@media (max-width: 991px) {
    #search-form-main__holder {
        #destination_summary, #dates-duration_summary, #guests_summary {
            margin-bottom: rem-calc(10);
            height: rem-calc(30);
        }

        .input-group {
            .input-group-prepend {
                margin-bottom: rem-calc(10);

                .btn {
                    padding-top: rem-calc(2);
                    padding-bottom: rem-calc(2);
                    line-height: rem-calc(24);
                }
            }
        }

        .search-btn__holder {
            position: relative;
            padding-left: rem-calc(35);

            .search-btn {
                width: 100%;
                padding-top: rem-calc(2);
                padding-bottom: rem-calc(2);
            }
        }

        #filter_options {
            font-size: rem-calc(18);
            line-height: rem-calc(25);
            color: $color-dark-gray;
            width: rem-calc(25);
            height: rem-calc(25);
        }
    }
}